import { Button, ButtonGroup } from '@mui/material';

interface Props {
    id : number;
    state : 'SELECTED' | 'UNSELECTED';
    overPct : number,
    underPct : number
}

const BetButtonGroup: React.FC<Props> = (props) => {
    if (props.state === 'SELECTED') {
        return (
            <ButtonGroup>
                <Button>{props.overPct}</Button>
                <Button>{props.underPct}</Button>
            </ButtonGroup>
        );
    } else {
        return (
            <ButtonGroup>
                <Button>Over</Button>
                <Button>Under</Button>
            </ButtonGroup>
        );
    }

}

export default BetButtonGroup;